"use client"
import React, { useState, useRef, useEffect } from 'react';
import { Card, CardContent } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Camera, X, Loader2 } from 'lucide-react';
import {toast} from 'react-hot-toast'

const AddCriminalPage = () => {
  const [isCapturing, setIsCapturing] = useState(false);
  const [capturedImage, setCapturedImage] = useState('');
  const [loading, setLoading] = useState(false);
  const videoRef = useRef<HTMLVideoElement | null>(null);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);

  const [formData, setFormData] = useState({
    name: '',
    dateOfBirth: '',
    caseNumber: '',
    charges: '',
    description: ''
  });

  useEffect(() => {
    const loadFaceApi = async () => {
      if (isCapturing) {
        try {
            const stream = await navigator.mediaDevices.getUserMedia({ video: true });
            if (videoRef.current && stream instanceof MediaStream) {
              videoRef.current.srcObject = stream;
          }
        } catch (err) {
          console.error('Error accessing camera:', err);
        }
      } else {
        const stream = videoRef.current?.srcObject as MediaStream;
        if (stream && stream instanceof MediaStream) {
          const tracks = stream.getTracks();
          tracks.forEach(track => track.stop());
      }
    };
}
    loadFaceApi();
    return () => {
        const stream = videoRef.current?.srcObject as MediaStream;
        if (stream && stream instanceof MediaStream) {
          const tracks = stream.getTracks();
          tracks.forEach(track => track.stop());
        }
      };
  }, [isCapturing]);

  const handleCapture = async () => {
    if (canvasRef.current && videoRef.current) {
      const context = canvasRef.current.getContext('2d');
      canvasRef.current.width = videoRef.current.videoWidth;
      canvasRef.current.height = videoRef.current.videoHeight;
      if(context)context.drawImage(videoRef.current, 0, 0);
      const imageData = canvasRef.current.toDataURL('image/jpeg');
      setCapturedImage(imageData);
      setIsCapturing(false);
    }
  };

  const handleSubmit = async () => {
    setLoading(true);
    try {
        console.log(capturedImage)
      const response = await fetch('/api/criminals/add', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...formData,
          faceImage: capturedImage
        }),
      });
      
      if (response.ok) {
        // Handle success
        alert('Record added successfully');
        setCapturedImage('');
        setFormData({
          name: '',
          dateOfBirth: '',
          caseNumber: '',
          charges: '',
          description: ''
        });
      } else {
        throw new Error('Failed to add record');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Failed to add record');
    toast.error("Failed to save record")
    }
    setLoading(false);
  };

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <Card className="max-w-4xl mx-auto">
        <CardContent className="p-6">
          <h1 className="text-2xl font-bold text-gray-900 mb-6">Add New Criminal Record</h1>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Form Fields */}
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-1">Full Name</label>
                <Input
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  placeholder="Enter full name"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium mb-1">Date of Birth</label>
                <Input
                  type="date"
                  value={formData.dateOfBirth}
                  onChange={(e) => setFormData({...formData, dateOfBirth: e.target.value})}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium mb-1">Case Number</label>
                <Input
                  value={formData.caseNumber}
                  onChange={(e) => setFormData({...formData, caseNumber: e.target.value})}
                  placeholder="Enter case number"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium mb-1">Charges</label>
                <Input
                  value={formData.charges}
                  onChange={(e) => setFormData({...formData, charges: e.target.value})}
                  placeholder="Enter charges"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium mb-1">Description</label>
                <Textarea
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                  placeholder="Enter description"
                  rows={4}
                />
              </div>
            </div>

            {/* Face Capture Section */}
            <div className="space-y-4">
              <div className="relative aspect-video bg-gray-100 rounded-lg overflow-hidden">
                {isCapturing ? (
                  <>
                    <video
                      ref={videoRef}
                      autoPlay
                      className="w-full h-full object-cover"
                    />
                    <Button 
                      className="absolute bottom-4 left-1/2 transform -translate-x-1/2"
                      onClick={handleCapture}
                    >
                      Capture Photo
                    </Button>
                  </>
                ) : capturedImage ? (
                  <div className="relative">
                    <img 
                      src={capturedImage} 
                      alt="Captured face" 
                      className="w-full h-full object-cover"
                    />
                    <Button 
                      variant="destructive"
                      size="icon"
                      className="absolute top-2 right-2"
                      onClick={() => setCapturedImage('')}
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                ) : (
                  <Button 
                    className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2"
                    onClick={() => setIsCapturing(true)}
                  >
                    <Camera className="mr-2 h-4 w-4" />
                    Start Camera
                  </Button>
                )}
              </div>
              <canvas ref={canvasRef} style={{ display: 'none' }} />
            </div>
          </div>

          <div className="mt-6 flex justify-end gap-4">
            <Button variant="outline" onClick={() => window.location.href = '/'}>
              Cancel
            </Button>
            <Button 
              onClick={handleSubmit} 
              disabled={!capturedImage || loading || !formData.name || !formData.caseNumber}
            >
              {loading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Saving...
                </>
              ) : (
                'Save Record'
              )}
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default AddCriminalPage;